<template>
<div>
  <div class="title">Icon展示表</div>
  <div class="icon">
    <div class="item" v-for="(item, index) in icons" :key="index">
      <svg-icon size="32" :icon-class="item" />
      <span>{{item}}</span>
    </div>
  </div>
  <button @click="setToken">111</button>
</div>
</template>

<script>
const requireAll = (requireContext) => requireContext.keys().map(requireContext);
const req = require.context("../assets/svg", false, /\.svg$/);
const icon = requireAll(req);
import { setToken } from "@/utils/auth";
export default {
  name: "Icon",
  computed: {
    icons() {
      console.log(icon);
      const name = icon.map(el => {
        const id = el.default.id;
        return id.substring((id.indexOf("-")) + 1, id.length);
      });
      return name;
    }
  },
  methods: {
    setToken() {
      setToken("");
    }
  }
};
</script>
<style lang="less" scoped>
.title {
  font-size: 32px;
  height: 60px;
  text-align: center;
}
.icon {
  height: calc(100vh - 120px);
  width: 1200px;
  margin: 0 auto;
  background: #ccc;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  .item {
    width: 120px;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span {
      margin-top: 10px;
    }
  }
}
</style>
